<template>
	<div class="exchangeRecord">
		<div class="exchangeRecord_head">
			<i class="icon_arrow_left" @click="goBack"></i>
			<p>兑换记录</p>
		</div>
		<div class="select_box">
			<p class="yearMonth" @click="showPicker"><span>{{ YearMonth }}</span><i class="icon_arrow_bottom"></i></p>
			<!-- <p class="yearMonth text_right" @click="showTypePicker"><span>{{ typeText }}</span> <i class="icon_arrow_bottom"></i></p> -->
		</div>
		<ul class="exchangeRecord_list">
			<li v-for="(item,index) in exOrderList" :key="item.orderTime">
				<div class="exchangeRecord_list_box">
					<img :src="item.commodityMainImg" alt="" class="exchangeRecord_list_img">
					<div>
						<p class="exchangeRecord_list_text1">{{ item.commodityName }}</p>
						<p class="exchangeRecord_list_text2">实际支付：<span>{{ item.amount }}积分</span></p>
						<p class="exchangeRecord_list_text3">兑换时间：<span>{{ new Date(item.completeTime).toLocaleString('chinese',{hour12:false}).replace(/\//g,'-') }}</span></p>
						<p class="exchangeRecord_list_text3">兑换编号：<span>{{ item.orderNo }}</span></p>
					</div>
				</div>
				<p class="status">{{ item.status }}</p>
			</li>
		</ul>
		<div v-show="exOrderList.length == 0">
		  <van-empty description="暂无记录" />
		</div>
		<div class="shadow" v-show="shadowFlag">
		  <div class="picker_box">
			<van-datetime-picker
			v-model="currentDate"
			type="year-month"
			title="选择年月"
			:min-date="minDate"
			:max-date="maxDate"
			@confirm="confirm"
			@cancel="cancel"
			:formatter="formatter"
			/>
		  </div>
		</div>
		<div class="shadow" v-show="shadowFlag1">
			<div class="picker_box">
				<van-picker
				  title="标题"
				  show-toolbar
				  :columns="columns"
				  @confirm="onConfirm"
				  @cancel="onCancel"
				/>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'exchangeRecord',
		data(){
			return {
				typeText:'全部',
				YearMonth:new Date().getFullYear()+'-'+(new Date().getMonth()+1 < 10 ? '0'+(new Date().getMonth()+1) : new Date().getMonth()+1),
				YearMonthDate:new Date(),
				shadowFlag:false,
				shadowFlag1:false,
				minDate: new Date(2018, 0, 1),
				maxDate: new Date(),
				currentDate: new Date(),
				columns: ['全部', '实物', '虚拟'],
				obj:{},
				exOrderList:[],
			}
		},
		mounted() {
			var jsonObj = localStorage.getItem("obj");
			this.obj = JSON.parse(jsonObj);
			this.exchangeOrderList();
		},
		methods:{
			goBack(){
			  this.$router.go(-1);
			},
			formatter(type, val) {
			  if (type === 'year') {
				return `${val}年`;
			  } else if (type === 'month') {
				return `${val}月`;
			  }
			  return val;
			},
			confirm(e){
				var YearMonth = e.getFullYear()+'-'+(e.getMonth()+1 < 10 ? '0'+(e.getMonth()+1) : e.getMonth()+1);
				this.YearMonth = YearMonth;
				this.YearMonthDate = e;
				this.exchangeOrderList();
				this.shadowFlag = false;
			},
			cancel(e){
				this.shadowFlag = false;
			},
			showPicker(){
				this.shadowFlag = true;
			},
			onConfirm(value, index) {
			  this.shadowFlag1 = false;
			  this.typeText = value;
			},
			onCancel() {
			  this.shadowFlag1 = false;
			},
			showTypePicker(){
				this.shadowFlag1 = true;
			},
			exchangeOrderList(){
				let _this = this;
				var exclusiveObj = {
					dateTime:_this.YearMonthDate,
					pageSize:'100',
					pageNum:'0',
				};
				console.log(exclusiveObj);
				var requestData = Object.assign(_this.obj,exclusiveObj);
				var redata = _this.byte256.byte256.encode(_this.key, JSON.stringify(requestData));
				_this.$axios({
					method: 'post',
					url: '/exchange/orderList',
					data:redata
				}).then(function(res) {
					console.log('orderList======',res);
					if(res.data.code === 200){
						var reData = JSON.parse(_this.byte256.byte256.decode(_this.reverse_key, res.data.data));
						console.log(reData);
						_this.exOrderList = reData;
					}
				});
			},
			toDetails(i){
				let _this = this;
				_this.$router.push({path:'/exchangeRecordDetails',query:{item:_this.exOrderList[i]}})
			}
		}
	}
</script>

<style scoped>
	.exchangeRecord{
		width: 10.8rem;
		min-height: 100vh;
		background-color: #fcebfe;
	}
	.exchangeRecord_head{
		width: 100%;
		height: 1.51rem;
		position: relative;
		background: linear-gradient(to left,#fe7773,#fb4377,#fa8dea);
		padding-top:var(--barHeight);
	}
	.icon_arrow_left{
		display: inline-block;
		vertical-align: middle;
		width:0.56rem;
		height:0.56rem;
		background-image: url(../static/img/icon_arrow_left.png);
		background-repeat: no-repeat;
		background-size: 100%;
		position: relative;
		z-index: 4;
		margin:0.47rem 0 0 0.33rem;
	}
	.exchangeRecord_head p{
		width: 100%;
		height: 1.51rem;
		line-height: 1.51rem;
		position: absolute;
		bottom:0;
		left:0;
		z-index: 2;
		text-align: center;
		color:#fff;
		font-size: 0.48rem;
	}
	.picker_box{
		width: 10.8rem;
		position: fixed;
		left:0;
		bottom:0;
		z-index: 100;
	}
	.yearMonth{
		font-size: 0.48rem;
		color: #000;
		line-height: 1.64rem;
		width: 10.8rem;
		height: 1.64rem;
		box-sizing: border-box;
		padding:0 0.64rem;
	}
	.yearMonth span{
		display: inline-block;
		vertical-align: middle;
	}
	.icon_arrow_bottom{
		display: inline-block;
		vertical-align: middle;
		width: 0.26rem;
		height:0.16rem;
		background-image: url(../static/img/icon_arrow_bottom.png);
		background-repeat: no-repeat;
		background-size: 100%;
		margin-left:0.3rem;
	}
	.select_box{
		display: flex;
		justify-content: space-between;
	}
	.text_right{
		text-align: right;
	}
	.exchangeRecord_list{
		width: 100%;
		box-sizing: border-box;
		padding: 0 0.33rem;
		position: relative;
	}
	.exchangeRecord_list li{
		border-radius: 0.25rem;
		width: 100%;
		background-color: #fff;
		box-sizing: border-box;
		padding:0.1rem;
		margin-bottom:0.3rem;
		box-shadow:0 0.09rem 0.09rem 0.02rem rgba(165,74,202,0.2);
		position: relative;
	}
	.exchangeRecord_list_title{
		box-sizing: border-box;
		padding:0 0.33rem;
		color: #000;
		line-height: 0.85rem;
		border-bottom:0.02rem solid #d2d2d2;
		font-weight: 600;
	}
	.exchangeRecord_list_box{
		box-sizing: border-box;
		padding:0.33rem;
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
	}
	.exchangeRecord_list_img{
		width: 2.5rem;
		height: 2.5rem;
		margin-right:0.2rem;
	}
	.exchangeRecord_list_box div{
		width:7rem;
	}
	.exchangeRecord_list_text1{
		font-weight: 600;
		font-size: 0.48rem;
		color:#232426;
		line-height: 0.76rem;
	}
	.exchangeRecord_list_text2{
		font-size: 0.36rem;
		color: #848383;
		line-height: 0.68rem;
	}
	.exchangeRecord_list_text2 span{
		font-size: 0.48rem;
		color:#ff588c;
	}
	.exchangeRecord_list_text3{
		font-size: 0.36rem;
		color: #848383;
		line-height: 0.68rem;
	}
	.exchangeRecord_list_text3 span{
		font-size: 0.36rem;
		color:#000;
	}
	.exchangeRecord_list .status{
		display: inline-block;
		color: #ff5a00;
		font-size: 0.36rem;
		position: absolute;
		right:0.64rem;
		top:0.32rem;
	}
</style>
